<template>
    <div>
    <form @submit.prevent="handleSubmit">
      <label for="">Email</label>
      <input type="email" placeholder="Email" v-model="email" required>

      <label for="">Password</label>
      <input type="password" placeholder="Password" v-model="password" required>
      <div v-if="passwordError" class="error">{{ passwordError }}</div>

      <label for="">Role</label>
      <select name="role" id="" v-model="role">
            <option value="developer"> web developer</option>
            <option value="designer"> web designer</option>
      </select>
      <div>
        <label >Skills</label>
        <input type="text" v-model="tempSkill"  @keyup="addup">
      </div>
      <div v-for="skill in skills" :key="skill" class="pill">
        <span @click="deleteSkill">
            {{ skill }}
        </span>
      </div>
      <div>
        <input type="checkbox" v-model="terms" required>
        <label for="">I agree to the terms and conditions</label>
      </div>
      <div class="submit">
        <button type="submit" class="submit">Create new account</button>
      </div>
    </form>
    <p>{{ email }}</p>
    <p>{{ password }}</p>

  </div>
</template>

<script>
export default {
    data(){
        return{
            email: '',
            password: '',
            role: '',
            terms: false,
            tempSkill: '',
            skills: [],
            passwordError: ''
        }
    },methods:{
        addup(e){
            // spacekey
            if(e.keyCode === 32 && this.tempSkill !== ' '){
                console.log('key up')
                if(!this.skills.includes(this.tempSkill)){
                    this.skills.push(this.tempSkill)
                }
                // this.skills.push(this.tempSkill)
                this.tempSkill = ''
            }
            // console.log('key up')
            // this.skills.push(event.target.value)
        },
        deleteSkill(e){
            console.log(e.target.innerText)
            this.skills = this.skills.filter(skill => skill !== e.target.innerText+' ')
        },
        handleSubmit(){
           this.passwordError = this.password.length < 6 ? 'Password must be at least 6 characters' : ''
            console.log("submitted")
        }
    }
}
</script>

<style>
    form{
        max-width: 420px;
        margin: 30px auto;
        background: white;
        text-align: left;
        padding: 40px;
        border-radius: 10px;
    }
    label{
        color: #aaa;
        display: inline-block;
        margin: 25px 0 15px;
        font-size: 0.6em;
        text-transform: uppercase;
        letter-spacing: 1px;
        font-weight: bold;
    }
    input,select{
        display:block;
        padding: 10px 6px;
        width: 100%;
        box-sizing: border-box;
        border:none;
        border-bottom:1px solid #ddd;
        color: #555;
    }
    input[type="checkbox"]{
        display: inline-block;
        width:16px;
        margin-right:0 10px 0 0;
        position: relative;
        top:2px;
    }
    .pill{
        display:inline-block;
        margin: 20px 10px 0 0;
        padding: 6px 12px;
        background: #eee;
        border-radius: 20px;
        font-size: 12px;
        letter-spacing: 1px;
        font-weight: bold;
        color: #777;
        cursor: pointer;
    }
    button{
        background: #0b6dff;
        border: 0;
        padding:10px 20px;
        margin-top:20px;
        color:white;
        border-radius: 20px;
    }
    .submit{
        text-align: center;
    }
</style>